import React from 'react';
import { AuthProvider, useIsAuthenticated } from 'react-auth-kit';
import { BrowserRouter as Router, Route, Routes, useLocation } from 'react-router-dom';
import { LoginPage } from './components/LoginPage';
import { About } from './components/About';
import { CaseStudy1 } from './components/CaseStudy1';
import { CaseStudy2 } from './components/CaseStudy2';
import { CaseStudy3 } from './components/CaseStudy3';
import { CaseStudy4 } from './components/CaseStudy4';
import { CaseStudy5 } from './components/CaseStudy5';
import Gallery from './components/Gallery';
import { Nav } from './components/Nav';
import { InternalNavigation } from './components/InternalNavigation';
import { StudyDetails1 } from './components/StudyDetails1';
import { StudyDetails2 } from './components/StudyDetails2';
import { StudyDetails3 } from './components/StudyDetails3';
import { StudyDetails4 } from './components/StudyDetails4';
import { Element } from 'react-scroll';


const Navigation = () => {
  const location = useLocation();
  const isHomePage = location.pathname === '/' || location.pathname === '/samportfolio/';
  return (
    <>
      {isHomePage ? <Nav /> : <InternalNavigation />}
    </>
  );
};

function App() {
  const isAuthenticated = useIsAuthenticated(); // Hook to check authentication

  // Debug: Check the authentication status
  console.log('Is authenticated:', isAuthenticated());

  // If not authenticated, show the login page
  if (!isAuthenticated()) {
    return <LoginPage />;
  }

  // Render the authenticated app content
  return (
    <Router>
      <Navigation />
      <Routes>
        <Route path="/" element={
          <>
            <Element name="About"><About /></Element>
            <Element name='CaseStudy1'><CaseStudy1 /></Element>
            <Element name='CaseStudy5'><CaseStudy5 /></Element>
            <Element name='CaseStudy2'><CaseStudy2 /></Element>
            <Element name='CaseStudy3'><CaseStudy3 /></Element>
            <Element name='CaseStudy4'><CaseStudy4 /></Element>
            <Element name='Gallery'><Gallery /></Element>
          </>
        }/>
        
        <Route path="/samportfolio/" element={
          <>
            <Element name="About"><About /></Element>
            <Element name='CaseStudy1'><CaseStudy1 /></Element>
            <Element name='CaseStudy5'><CaseStudy5 /></Element>
            <Element name='CaseStudy2'><CaseStudy2 /></Element>
            <Element name='CaseStudy3'><CaseStudy3 /></Element>
          </>
        }/>

        <Route path="/zubopay" element={<StudyDetails2 />} />
        <Route path="/marygold" element={<StudyDetails1 />} />
        <Route path="/website" element={<StudyDetails3 />} />
        <Route path="/taxirankapp" element={<StudyDetails4 />} />
      </Routes>
    </Router>
  );
}

// RootApp component to include AuthProvider
function RootApp() {
  return (
    <AuthProvider
      authType="localstorage"
      authName="_auth"
      cookieDomain={window.location.hostname}
      cookieSecure={window.location.protocol === 'https:'}
    >
      <App />
    </AuthProvider>
  );
}

export default RootApp;























