import React, { useState } from 'react';
import { useSignIn } from 'react-auth-kit'; // Import useSignIn
import styles from './LoginPage.module.css';

export const LoginPage = () => {
  const [password, setPassword] = useState('');
  const signIn = useSignIn(); // Hook for signing in
  
  // Array of correct passwords
  const correctPasswords = ['productManagerproductDesigner', 'DesignForGood', 'ProductDesignAccess'];

  const handleLogin = () => {
    // Check if the entered password is in the correctPasswords array
    if (correctPasswords.includes(password)) {
      const success = signIn({
        token: 'dummyAuthToken', 
        expiresIn: 3600, 
        tokenType: 'Bearer',
        authState: { user: 'authorizedUser' }, // Store any state you need
      });

      if (!success) {
        alert('Sign-in failed');
      }
    } else {
      alert('Incorrect password');
    }
  };

  return (
    <div className={styles.loginContainer}>
      <h1>Hi, Thanks for stopping by!</h1>
      <h2><b style={{ color: '#558733', fontWeight: 'Black' }}>Password: </b>On the top right Corner of my CV</h2>
      <div className={styles.inputContainer}>
        <input
          type="password"
          placeholder="Password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
      </div>
      <button onClick={handleLogin}>Continue</button>
    </div>
  );
};





